import CIcon from "@coreui/icons-react";
import { freeSet } from "@coreui/icons";

import {
  CCol,
  CRow,
  CCard,
  CCardTitle,
  CCardBody,
  CDataTable,
  CDropdown,
  CDropdownToggle,
  CDropdownMenu,
  CDropdownItem,
  CModal,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CButton,
  CCardHeader,
  CAlert,
} from "@coreui/react";
import DeleteForeverIcon from "@material-ui/icons/DeleteForever";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "../../localyStore";
import { useHistory } from "react-router-dom";
import "./product.css";

const ProductLists = () => {
  const history = useHistory();

  const userId = useSelector((state) => state.user._id);
  const { role, token } = useSelector(selectUser);

  const [loading, setLoading] = useState(true);
  const [offersShow, setOffersShow] = useState(false);
  const [offers, setOffers] = useState([]);
  const [flashSale, setFlashSale] = useState([]);
  const [selectedOffer, setSelectedOffer] = useState(null);
  const [discount, setDiscount] = useState(1);

  const [products, setProducts] = useState();
  const [vendorProducts, setvendorProducts] = useState();
  const [vendorProductsPending, setVendorProductsPending] = useState("pending");

  const server = process.env.REACT_APP_SERVER;
  const [reload, setReload] = useState(null);

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  useEffect(() => {
    let getProduct;
    if (role === "admin") {
      getProduct = process.env.REACT_APP_ADMIN_PRODUCT_LIST_GET;
      axios
        .get(server + process.env.REACT_APP_ADMIN_GET_OFFERS, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then((res) => {
          setOffers(res.data.offers);
          setLoading(false);
        })
        .catch((err) => {
          setLoading(false);
          setEvent({
            type: "error",
            massage: err?.response?.data.message ?? "server-error",
          });
          clearEvent(5000);
        });
      axios
        .get(server + process.env.REACT_APP_ADMIN_GET_FLASH_SALE, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then((res) => {
          setFlashSale(res.data.flashSales[0]);
        })
        .catch((err) => {
          setEvent({
            type: "error",
            massage: err?.response?.data.message ?? "server-error",
          });
          clearEvent(5000);
        });
    }
    if (role === "vendor") {
      getProduct = process.env.REACT_APP_ADMIN_PRODUCT_VENDOR_LIST_GET;
    }
    axios
      .get(server + getProduct, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        if (role === "admin") {
          dataChange(res?.data.products);
        }
        if (role === "vendor") {
          dataChange(res?.data.vendorProductPending);
          setvendorProducts(res.data);
        }
        setLoading(false);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  }, [token, role, userId, reload]);
  // change data
  const dataChange = (data) => {
    let newData = data.map((i) => ({
      _id: i._id,
      ProductImg: i.productPictures?.[0]?.img,
      name: i.name,
      price: Math.round(i.price),
      quantity: Math.round(i.quantity),
      discountPrice: Math.round(i.discountPrice),
      finalPrice: Math.round(i.finalPrice),
      slug: i.slug,
      sku: i.sku_number,
      category:
        i.sub2Category?.name || i.sub1Category?.name || i.mainCategory?.name,
      verifiedVendors: i?.verifiedVendors?.name ?? i.createdBy?.name,
    }));
    setProducts(newData);
  };
  // delete products
  const deleteProduct = (id) => {
    axios
      .delete(server + process.env.REACT_APP_ADMIN_PRODUCT_DELETE + id, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => {
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
        setReload(!reload);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // toggle modal
  const toggle = (offer = null, product = null) => {
    setOffersShow(!offersShow);
    setSelectedOffer({ ...offer, product });
  };

  // add products to offers
  const addProductToOffers = () => {
    let api;
    if (selectedOffer.flashSalesTitle) {
      api = process.env.REACT_APP_ADMIN_ADD_PRODUCT_FLASH_SALE;
    } else {
      api = process.env.REACT_APP_ADMIN_PUSH_OFFERS;
    }
    axios
      .post(
        server + api,
        {
          offerId: selectedOffer._id,
          product: selectedOffer.product._id,
          discount,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then((res) => {
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(8000);
        toggle();
        setDiscount(1);
        setSelectedOffer(null);
      })
      .catch((err) => {
        toggle();
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  let feilds;
  if (role === "admin") {
    feilds = [
      {
        key: "sku",
        label: "SKU",
        _style: { width: "9%" },
        sorter: false,
        filter: true,
      },
      {
        key: "ProductImg",
        label: "Image",
        sorter: false,
        filter: false,
      },
      {
        key: "slug",
        label: "Name",
        sorter: true,
        filter: true,
      },
      "price",
      "discountPrice",
      "finalPrice",
      "category",
      "quantity",
      {
        key: "verifiedVendors",
        label: "CreatedBy",
        _style: { width: "6%" },
        sorter: true,
        filter: true,
      },
      {
        key: "offers",
        label: "Offer",
        _style: { width: "6%" },
        sorter: false,
        filter: false,
      },
      {
        key: "Action",
        label: "",
        _style: { width: "6%" },
        sorter: false,
        filter: false,
      },
    ];
  }
  if (role === "vendor") {
    if (vendorProductsPending === "pending") {
      feilds = [
        {
          key: "sku",
          label: "SKU",
          _style: { width: "9%" },
          sorter: false,
          filter: true,
        },
        {
          key: "ProductImg",
          label: "Image",
          sorter: false,
          filter: false,
        },
        {
          key: "slug",
          label: "Name",
          sorter: true,
          filter: true,
        },
        "price",
        "category",
        "quantity",
        {
          key: "Action",
          label: "",
          _style: { width: "6%" },
          sorter: false,
          filter: false,
        },
      ];
    } else {
      feilds = [
        {
          key: "sku",
          label: "SKU",
          _style: { width: "9%" },
          sorter: false,
          filter: true,
        },
        {
          key: "ProductImg",
          label: "Image",
          sorter: false,
          filter: false,
        },
        {
          key: "slug",
          label: "Name",
          sorter: true,
          filter: true,
        },
        "price",
        "category",
        "quantity",
      ];
    }
  }
  // vendor only change product type
  const changeTypeProduct = (e) => {
    setVendorProductsPending(e.target.value);
    if (e.target.value === "pending") {
      dataChange(vendorProducts.vendorProductPending);
    } else {
      dataChange(vendorProducts.vendorProductApproved);
    }
  };
  return (
    <div className="productlist">
      <CRow>
        <CCol xl={12}>
          <CCard>
            {role === "vendor" && (
              <CCardHeader>
                <select onChange={changeTypeProduct}>
                  <option value="pending">Pending</option>
                  <option value="approved">Approved</option>
                </select>
              </CCardHeader>
            )}
            <CCardTitle className="text-center mt-4">
              {role === "admin"
                ? "Product Lists"
                : vendorProductsPending === "pending"
                ? "Pending Products"
                : "Approved Products"}
            </CCardTitle>
            <CCardBody>
              <CDataTable
                tableFilter
                columnFilter
                sorter
                pagination
                itemsPerPageSelect
                loading={loading}
                items={products}
                fields={feilds}
                hover
                striped
                itemsPerPage={20}
                // onRowClick={(item) => history.push(`/product/${item._id}`)}
                scopedSlots={{
                  "SiNo.": (item, index) => <td key={index}>{index + 1}</td>,
                  ProductImg: (item) => (
                    <td>
                      <img
                        className="productlist_img"
                        loading="lazy"
                        src={item.ProductImg}
                        alt={item.name}
                      />
                    </td>
                  ),
                  offers: (item) => (
                    <td>
                      <CDropdown>
                        <CDropdownToggle>
                          <CIcon content={freeSet.cilGift} size="2xl" />
                        </CDropdownToggle>
                        <CDropdownMenu>
                          <h5 className="center my-2">Add Offres</h5>
                          {offers?.map((offers) => (
                            <CDropdownItem
                              key={offers._id}
                              onClick={() => toggle(offers, item)}
                            >
                              {offers.offerTitle}
                            </CDropdownItem>
                          ))}
                          <h5 className="center my-2">Add FlashSale</h5>
                          {flashSale && (
                            <CDropdownItem
                              key={flashSale?._id}
                              onClick={() =>
                                toggle(
                                  {
                                    ...flashSale,
                                    offerTitle: flashSale?.flashSalesTitle,
                                  },
                                  item
                                )
                              }
                            >
                              {flashSale.flashSalesTitle}
                            </CDropdownItem>
                          )}
                        </CDropdownMenu>
                      </CDropdown>
                    </td>
                  ),
                  Action: (item) => (
                    <td>
                      <CDropdown>
                        <CDropdownToggle>
                          <CIcon name="cilSettings" size="2xl" />
                        </CDropdownToggle>
                        <CDropdownMenu>
                          <CDropdownItem
                            onClick={() =>
                              history.push(`/productlist/${item._id}`)
                            }
                          >
                            <NoteAddIcon /> Edit
                          </CDropdownItem>
                          <CDropdownItem
                            onClick={() => deleteProduct(item._id)}
                          >
                            <DeleteForeverIcon /> Delete
                          </CDropdownItem>
                        </CDropdownMenu>
                      </CDropdown>
                    </td>
                  ),
                }}
              />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CModal className="sm-30" show={offersShow} onClose={toggle}>
        <CModalHeader closeButton>
          Add {selectedOffer?.product?.name} To {selectedOffer?.offerTitle}
        </CModalHeader>
        <CModalBody>
          <div className="form-group">
            <label htmlFor="exampleFormControlFile2">Offer Title</label>
            <br />
            <input
              type="text"
              name="offerTitle"
              id="exampleFormControlFile2"
              value={selectedOffer?.offerTitle}
              disabled
            />
          </div>
          <div className="form-group">
            <label htmlFor="exampleFormControlFile2">
              Product Discount in %
            </label>
            <br />
            <input
              type="number"
              name="discount"
              id="exampleFormControlFile2"
              min="1"
              max="100"
              onChange={(e) => setDiscount(e.target.value)}
              value={discount}
            />
          </div>
        </CModalBody>
        <CModalFooter>
          <CButton onClick={toggle} color="primary">
            Cancel
          </CButton>
          <CButton onClick={addProductToOffers} color="primary">
            Confirm
          </CButton>
        </CModalFooter>
      </CModal>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};
export default ProductLists;
